.copyright__text {
  @include base-font(16px, 1, $greyish-brown, $base-font);
  padding-top: 5px;
  padding-right: 23px;
}

.copyright__links {
  @include flex-row();
  width: 100%;
  text-decoration: none;
  padding-top: 22px;
  border-top: 1px solid $light-pink;
  align-items: top;
}

.copyright__logo-academy {
  fill: $dark-gray;
}

@media (min-width: $tablet-width) {
  .copyright__links {
    padding-top: 0;
    border-top: none;
  }
}
